<template>
  <!-- hash -->
  <h2 v-if="item.type === 'hash'" class="item-hash" :id="'prefix' + calculateHash(item.text) | generateId"
    :data-text="item.text">
    <router-link class="header-anchor" :to="{ path: $route.path, hash: `#${item.text}` }">
      {{ item.text }}
    </router-link>

  </h2>
  <div v-else class="content-item">
    <!-- title -->
    <p v-if="item.type === 'title'" class="item-title">{{ item.text }} </p>


    <div v-if="item.text === ''" style="margin-top: 15px;">

      <!-- <i style="font-size:larger;color: #409EFF;" class="el-icon-s-opportunity"></i> -->
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">1. API KEY</el-link> <i
        class="el-icon-right"></i>
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">2. API KEY</el-link> <i
        class="el-icon-right"></i>
      <el-link
        href="https://bingx-api.github.io/docs/#/zh-cn/swapV2/authentication.html#%E7%AD%BE%E5%90%8D%E8%AF%B4%E6%98%8E"
        target="_blank">3. </el-link> <i class="el-icon-right"></i>
      <i class="el-icon-right">4. </i>
      <i class="el-icon-right"></i>
      <el-link
        href="https://bingx-api.github.io/docs/#/zh-cn/swapV2/base-info.html#%E5%B8%B8%E8%A7%81%E9%94%99%E8%AF%AF%E7%A0%81"
        target="_blank">5. </el-link>
      <i class="el-icon-right"></i>
      <el-link href="https://bingx-api.github.io/docs/#/zh-cn/swapV2/base-info.html#%E9%A2%91%E7%8E%87%E9%99%90%E5%88%B6"
        target="_blank">6. </el-link> <i class="el-icon-right"></i>
      <el-link
        href="https://bingx-api.github.io/docs/#/zh-cn/swapV2/base-info.html#%E6%97%B6%E9%97%B4%E6%88%B3%E8%A7%84%E8%8C%83"
        target="_blank">7.
        </el-link><i class="el-icon-right"></i>
      <el-link href="https://bingx.com/zh-tw/support/articles/360027240173/#h_01GC8VAS3VM1RHFEDE9XVTDAJQ"
        target="_blank">8. </el-link>
    </div>
    <div v-if="item.text === ''" style="margin-top: 15px;">
      <!-- <i style="font-size:larger;color: #409EFF;" class="el-icon-s-opportunity"></i> -->
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">1. API KEY</el-link> <i
        class="el-icon-right"></i>
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">2. API KEY</el-link> <i
        class="el-icon-right"></i>
      <el-link
        href="https://bingx-api.github.io/docs/#/zh-tw/swapV2/authentication.html#%E7%B0%BD%E5%90%8D%E8%AA%AA%E6%98%8E"
        target="_blank">3. </el-link> <i class="el-icon-right"></i>
      <i class="el-icon-right">4. </i>
      <i class="el-icon-right"></i>
      <el-link
        href="https://bingx-api.github.io/docs/#/zh-tw/swapV2/base-info.html#%E5%B8%B8%E8%A6%8B%E9%8C%AF%E8%AA%A4%E7%A2%BC"
        target="_blank">5. </el-link> <i class="el-icon-right"></i>
      <el-link href="https://bingx-api.github.io/docs/#/zh-tw/swapV2/base-info.html#%E9%A0%BB%E7%8E%87%E9%99%90%E5%88%B6"
        target="_blank">6. </el-link> <i class="el-icon-right"></i>
      <el-link
        href="https://bingx-api.github.io/docs/#/zh-tw/swapV2/base-info.html#%E6%99%82%E9%96%93%E6%88%B3%E8%A6%8F%E7%AF%84"
        target="_blank">7.
        </el-link><i class="el-icon-right"></i>
      <el-link href="https://bingx.com/zh-tw/support/costs/"
        target="_blank">8. </el-link>

    </div>
    <div v-if="item.text === 'request parameters'" style="margin-top: 15px;">
      <!-- <i style="font-size:larger;color: #409EFF;" class="el-icon-s-opportunity"></i> -->
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">1. Create API KEY</el-link> <i
        class="el-icon-right"></i>
      <el-link href="https://bingx.com/zh-tw/account/api/" target="_blank">2. Configure API KEY permissions</el-link> <i
        class="el-icon-right"></i>
      <el-link href="https://bingx-api.github.io/docs/#/en-us/swapV2/authentication.html#Signature%20Description"
        target="_blank">3. Understanding
        signature authentication</el-link>
      <i class="el-icon-right">4. Run the following example code</i>
      <i class="el-icon-right"></i>
      <el-link href="https://bingx-api.github.io/docs/#/en-us/swapV2/base-info.html#Common%20Error%20Codes"
        target="_blank">5.
        Understand common error codes</el-link> <i class="el-icon-right"></i>
      <el-link href="https://bingx-api.github.io/docs/#/en-us/swapV2/base-info.html#Rate%20limit" target="_blank">6.
        Understand
        rate limitations</el-link> <i class="el-icon-right"></i>
      <el-link href="https://bingx-api.github.io/docs/#/en-us/swapV2/base-info.html#Timestamp" target="_blank">7.
        Understanding
        request timestamps</el-link><i class="el-icon-right"></i>
      <el-link href="https://bingx.com/en-us/support/costs/"
        target="_blank">8. Understand fee schedule</el-link>

    </div>
    <!-- </div> -->
    <!--  -->
    <div v-if="item.type === 'normal'" class="item-normal">
      <p :style="item.style || {}" v-if="item.text">{{ item.text }}<label
          v-if="item.text === 'HTTP ' || item.text === 'HTTP ' || item.text === 'HTTP Request'">
          https://open-api.bingx.com</label>
      </p>
      <ul v-if="Array.isArray(item.children)" style="margin-left: -10px;">
        <li v-for="(c1, i) in item.children" :key="i">
          <span v-if="c1.type === 'html'" v-html="c1.text" />
          <span v-else>{{ c1.text }}</span>
          <ul v-if="Array.isArray(c1.children)" style="margin-left: -15px; list-style-type: disc">
            <li v-for="(c2, j) in c1.children" :key="j">{{ c2.text }}</li>
          </ul>
        </li>
      </ul>

    </div>

    <!-- html  -->
    <p v-if="item.type === 'html'" v-html="item.text"></p>

    <!-- warning  -->
    <p v-if="item.type === 'warning'" class="item-warning-content">{{ item.text }}</p>

    <!-- code  -->
    <p v-if="item.type === 'code' && item.text.indexOf('/openApi') < 0" class="item-code-content"
      style="position: relative">
      <i class="el-icon-copy-document" v-clipboard:copy="item.text" v-clipboard:success="onCopy"
        v-clipboard:error="onError" />
      {{ item.text }}
    </p>
    <div style="margin-bottom: 5px;" v-if="item.type === 'demo' && isApi(item)" :lang="lang" :item="item">
      <p class="item-code-content" style="position: relative">
        <i class="el-icon-copy-document" v-clipboard:copy="item.api.uri" v-clipboard:success="onCopy"
          v-clipboard:error="onError" />
        {{ item.api.method + " " + item.api.uri }}
      </p>
      <div style="font-weight: bold;" v-if="lang == 'zh-tw'">
        API KEY:
        <el-link target="_blank" href="https://bingx.com/zh-tw/account/api/" type="primary">{{
          getAuth(lang, item) }}</el-link>
      </div>
      <div style="font-weight: bold;" v-else>
        API KEY permission:
        <el-link target="_blank" href="https://bingx.com/en-us/account/api/" type="primary">{{ getAuth(lang, item)
        }}</el-link>
      </div>


      <label
        style="font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif">Content-Type:</label>
      <label v-if="supportJson(item)"> <el-button @click="authenticationPage()" type="text">request
          body(application/json)</el-button>
        <el-button @click="authenticationPage()" type="text" disabled>query string</el-button>
      </label>
      <label v-else> <el-button @click="authenticationPage()" type="text" disabled>request
          body(application/json)</el-button>
        <el-button @click="authenticationPage()" type="text">query string</el-button>
      </label>

    </div>
    <!-- demo  -->
    <Demo v-if="item.type === 'demo' && $router.currentRoute.path.indexOf('/socket/') < 0 && !supportJson(item)"
      :lang="lang" :item="item" />
    <DemoJson v-if="item.type === 'demo' && $router.currentRoute.path.indexOf('/socket/') < 0 && supportJson(item)"
      :lang="lang" :item="item" />
    <DemoSocket v-if="item.type === 'demo' && $router.currentRoute.path.indexOf('/socket/') > -1" :lang="lang"
      :item="item" />


  </div>
</template>

<script>
import { msgMap, generateId, apikeyList } from '@/utils'

import Demo from './Demo.vue'
import crypto from 'crypto'
import DemoSocket from './DemoSocket.vue'
import DemoJson from './DemoJson.vue'
// const crypto = require('crypto');

// console.log(apikeyList)

export default {
  name: "ContentItem",
  props: {
    lang: String,
    item: {
      type: Object,
      default: () => { }
    },
    contentArray: Array
  },

  components: {
    Demo,
    DemoSocket,
    DemoJson,
  },
  data() {
    return {

    }
  },

  filters: {
    generateId
  },
  computed: {
    successMsg() {
      return msgMap['success'][this.lang]
    },

    errorMsg() {
      return msgMap['error'][this.lang]
    },
  },

  methods: {
    getAuth(lang, item) {

      // 1	
      // 2	
      // 3	
      // 4	
      // 5	
      // 6	
      // 7	
      const apiKeyPer = {
        "tw": ["API KEY", "", "", "", "", "", "", ""],
        "en": ["No API KEY signature required", "Spot Trading", "Read", "Perpetual Futures Trading", "Universal Transfer", "Withdraw", "Manage Subaccounts", "Internal Transfers Between Subaccounts"]
      }
      let authNumber = "0"
      apikeyList.forEach((data) => {

        if (data.resource == item.api.uri) {
          if (data.method.toUpperCase() == item.api.method.toUpperCase()) {
            if (data.auth_type == "0") {
              authNumber = "0"
            } else {
              authNumber = data.business_id
            }
          }
          if (data.method == "null") {
            authNumber = data.business_id
          }
          return
        }
      })
      const authNumberInt = Number(authNumber);
      if (lang == "zh-tw") {
        return apiKeyPer.tw[authNumberInt]
      }
      return apiKeyPer.en[authNumberInt]
    },
    authenticationPage() {
      const arr = window.location.href.split("/#/")[1].split("/")
      let path = "/" + arr[0] + "/" + arr[1] + "/authentication.html"
      if (this.lang == "zh-hans") {
        path += "#"
      }
      if (this.lang == "zh-tw") {
        path += "#"
      }
      if (this.lang == "en-US") {
        path += "#Signature%20Description"
      }
      this.$router.push({ path: path })
    },
    supportJson(item) {
      let support = false
      const jsonList = [
        "/openApi/subAccount/v1/create",
        "/openApi/subAccount/v1/apiKey/create",
        "/openApi/subAccount/v1/apiKey/edit",
        "/openApi/subAccount/v1/apiKey/del",
        "/openApi/subAccount/v1/updateStatus",
      ]
      if (jsonList.includes(item.api.uri)) {
        support = true
      }
      return support
    },
    isApi(item) {
      let api = false
      if (item.api && item.api.url != "" && item.api.method != "" && item.api.method != undefined) {
        api = true
      }
      return api
    },
    calculateHash(data) {
      const hash = crypto.createHash('md5');
      hash.update(data);
      return hash.digest('hex');
    },
    onCopy() {
      this.$message.success(this.successMsg)
    },
    onError() {
      this.$message.success(this.errorMsg)
    },
  },
}
</script>

<style>
.content-item {}

.item-hash {
  margin-top: 40px;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: #000000;
}

.item-hash a,
.item-hash a:hover {
  text-decoration: none;
  color: #000000;
}

.item-title {
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
  color: black;
  margin: 20px 0px;
}

.item-warning-content {
  display: inline-block;
  /* margin: 16px 0px; */
  margin: 16px 0 0 0;
  background: #FFD677;
  border-radius: 8px;
  padding: 16px;
}

.span-code {
  padding: 2px 4px;
  color: #1f1e24;
  background-color: #ececef;
  border-radius: 4px;
}

.item-code-content {
  background: #F9F9F9;
  border-radius: 8px;
  padding: 24px;
}

.item-code-content i {
  position: absolute;
  top: 5px;
  right: 10px;
  cursor: pointer;
}
</style>


